#page-notice-details {
    .notice-cont-detail {
        position: relative;
        width: 70%;
        margin: auto;
        @media (max-width: 1025px) {
            width: 90%;
        }
        @media (max-width: 800px) {
            width: 100%;
        }

        .share.box-shadow {
            .item-content {
                .top {
                    img {
                        height: 400px;
                        @media (max-width: 768px) {
                            height: 100%;
                        }
                    }
                }
            }
        }

        #item-notice .item-content .top .txt_con {
            line-height: 18px;
        }
    }
}
